<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar">
			<el-form :inline="true" :model="formInline" class="demo-form-inline">
				<el-form-item>
					<el-input v-model="formInline.keywords" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button v-on:click="search"></el-button>
				</el-form-item><!--
				<el-form-item>
					<el-button @click="handleAdd"></el-button>
				</el-form-item>-->
			</el-form>
		</el-col>

		<!---->
		<template>
			<el-table :data="tableData" highlight-current-row v-loading="listLoading" style="width: 100%;">
				<el-table-column type="index" width="50">
				</el-table-column>
				<el-table-column prop="name" label="" width="100" sortable>
				</el-table-column>
				<el-table-column prop="m_name" label="" width="100" sortable>
				</el-table-column>
				<el-table-column prop="_status" label="" :formatter="formaterStatus" width="100" sortable>
				</el-table-column>
				<el-table-column prop="price" label="" width="100" sortable>
				</el-table-column>
				<el-table-column prop="created_at" label=""  sortable>
				</el-table-column>
				<el-table-column prop="uuid" label=""  sortable>
				</el-table-column>
				<el-table-column inline-template :context="_self" label="" width="100">
					<span>
						<el-button type="text" size="small" @click="handleDetail(row)"></el-button>
						<el-button :id="tableData.id" type="text" size="small" @click="handleEdit(row)"></el-button>
					</span>
				</el-table-column>
			</el-table>
		</template>

		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom:10px;">
			<el-pagination @size-change="handleSizeChange"
 @current-change="handleCurrentChange" :current-page="1" :page-sizes="[10, 20, 30, 40]" :page-size="10" layout="total, prev, pager, next, jumper"
				:total="total" style="float:right">
			</el-pagination>
		</el-col>


		<el-dialog :title="detailFormTtile" v-model="detailFormVisible" :close-on-click-modal="false">
			<el-table :data="detailData">
			    <el-table-column property="order_id" label="id" width="100"></el-table-column>
			    <el-table-column property="service_detail_id" label="id" width="100"></el-table-column>
			    <el-table-column property="price" label="" width="100"></el-table-column>
				<el-table-column property="content" label="" width="100"></el-table-column>
			    <el-table-column property="created_at" label="" :formatter="formatDate"  ></el-table-column>
			</el-table>
		</el-dialog>

		<!---->
		<el-dialog :title="editFormTtile" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="status">
					<el-radio-group v-model="editForm.status">
					    <el-radio :label="0"></el-radio>
					    <el-radio :label="1"></el-radio>
					    <el-radio :label="2"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="" prop="message">
					<el-input type="textarea" v-model="editForm.message"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"> </el-button>
				<el-button type="primary" @click="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
			</div>
		</el-dialog>

	</section>
</template>

<script>
	import util from '../../common/util'
	import NProgress from 'nprogress'

  export default {
    data() {
      return {
      			ordersDetailUrl:function (obj){
      				return 'http://shama.jcjever.com/order_detail?page=1&order_id='+obj.id;
      			},
				DetailUril: (obj) => {
					return 'http://shama.jcjever.com/orders/'+obj.id;
				},
	  			ListUrl: function (obj){
	  				if(!obj.page)
	  					obj.page = 1;
	  				if(!obj.keywords)
	  					obj.keywords = '';
	  				return 'http://shama.jcjever.com/orders?page=' + obj.page + '&keywords=' + obj.keywords;

				},
				formInline: {
					keywords: ''
				},
				pickerOptions0: {
					disabledDate(time) {
						return time.getTime() < Date.now() - 8.64e7;
					}
				},
				value1:'',
				editFormVisible:false,//
				detailFormVisible:false,//
				editFormTtile:'',//
				detailFormTtile:'',
				//
				editForm: {
					id:0,
					status: 0,
					message: ''
				},
				editLoading:false,
				btnEditText:' ',
				editFormRules:{
					message:[
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				tableData: [],
				detailData:[],
				total:0,
				listLoading:false
     		}
    },
	mounted: function (){
		this.loadData({page:1});
	},
    methods: {
   			search(){
    			if(this.formInline.keywords){
    				this.loadData({keywords:this.formInline.keywords});
    			}
    		},
			handleCurrentChange(val) {
				this.currentPage = val;
				console.log(`: ${val}`);
				this.loadData({pphone:val});
			},
			handleSizeChange(val) {
			   console.log(` ${val} `);
			},
			requestError:function(response) {
				this.$notify.error({
				  title: '',
				  messphone: ''
				});
			},
			update(obj){
				this.$http.put(this.DetailUril(obj),{status:obj.status,message:obj.message}).then((response) => {
					console.log('update success');
				}).catch(this.requestError);
			},
			loadData(obj){
				this.$http.get(this.ListUrl(obj)).then((response) => {
				 	this.tableData = response.body.data;
					this.total = response.body.total;
				}).catch(this.requestError);
			},
			//
			formatDate:function(row,column){
				return row.created_at = row.created_at.substring(0,10);
			},
			formaterStatus:function (row,column){
				switch(row.status){
					case 0:
						row._status='';
						break;
					case 1:
						row._status='';
						break;
					case 2:
						row._status='';
						break;
				}
				return row._status;
			},
			handleDetail:function(row){
				this.detailFormVisible=true;
				this.detailData = this.loadDetail(row.id);
			},
			loadDetail:function(id){
				this.$http.get(this.ordersDetailUrl({id:id})).then((response) => {
				 	this.detailData = response.body.data;
				}).catch(this.requestError);
			},
			//
			handleDel:function(row){
				//console.log(row);
				var _this=this;
				this.$confirm('?', '', {
					//type: 'warning'
				}).then(() => {
					_this.listLoading=true;
					NProgress.start();
					setTimeout(function(){
						for(var i=0;i<_this.tableData.length;i++){
							if(_this.tableData[i].id==row.id){
								_this.tableData.splice(i,1);

								_this.listLoading=false;
								NProgress.done();
								_this.$notify({
									title: '',
									messphone: '',
									type: 'success'
								});

								break;
							}
						}
					},1000);
				}).catch(() => {

				});
			},
			//
			handleEdit:function(row){
				console.log(row.status);
				this.editFormVisible=true;
				this.editFormTtile='';
				this.editForm.id=row.id;
				this.editForm.status= row.status;
				this.editForm.message=row.message;
				this.editForm.name=row.name;
			},
			// or 
			editSubmit:function(){
				var _this=this;
				//console.log(_this.$refs.editForm);
				_this.$refs.editForm.validate((valid)=>{
					console.log(valid);
					if(valid){
						_this.$confirm('','',{}).then(()=>{
							_this.editLoading=true;
							NProgress.start();
							_this.btnEditText='';
							setTimeout(function(){
								_this.update({id:_this.editForm.id,status:_this.editForm.status,message:_this.editForm.message});
								_this.editLoading=false;
								NProgress.done();
								_this.btnEditText=' ';
								_this.$notify({
									title: '',
									messphone: '',
									type: 'success'
								});
								_this.editFormVisible = false;

								if(_this.editForm.id==0){
									//
									_this.tableData.push({
										id:new Date().getTime(),
										name: _this.editForm.name
									});
								}else{
									//
									for(var i=0;i<_this.tableData.length;i++){
										if(_this.tableData[i].id==_this.editForm.id){
											_this.tableData[i].status=_this.editForm.status;
											_this.tableData[i].message=_this.editForm.message;
											console.log(_this.editForm.status);
											console.log(_this.editForm.message);
											break;
										}
									}
								}
							},1000);

						});

					}
				});

			},
			//
			handleAdd:function(){
				var _this=this;

				this.editFormVisible=true;
				this.editFormTtile='';

				this.editForm.id=0;
				this.editForm.name='';
			}
    }
  }
</script>

<style scoped>
	.toolbar .el-form-item {
		margin-bottom: 10px;
	}

	.toolbar {
		background: #fff;
		padding: 10px 10px 0px 10px;
	}
</style>



