<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar">
			<el-form :inline="true" :model="formInline" class="demo-form-inline">
				<el-form-item>
					<el-input v-model="formInline.keywords" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button v-on:click="search" ></el-button>
				</el-form-item>
				<el-form-item>
					<el-button v-on:click="_export" ></el-button>
				</el-form-item>
				<!--
				<el-form-item>
					<el-button @click="handleAdd"></el-button>
				</el-form-item>
				-->
			</el-form>
		</el-col>

		<!---->
		<template>
			<el-table :data="tableData" highlight-current-row v-loading="listLoading" style="width: 100%;">
				<el-table-column type="index" width="50">
				</el-table-column>
				<el-table-column prop="name" label="" width="100" sortable>
				</el-table-column>
				<el-table-column prop="sex" label="" width="100" :formatter="formatSex" sortable>
				</el-table-column>
				<el-table-column prop="phone" label="" width="130" sortable>
				</el-table-column>
				<el-table-column prop="birthday" label="" width="120" :formatter="formatBirth"  sortable>
				</el-table-column>
				<el-table-column prop="email" label="" sortable>
				</el-table-column>
				<el-table-column prop="card_id" label="" sortable>
				</el-table-column>
				<el-table-column inline-template :context="_self" label="" width="100">
					<span>
						<!--<el-button type="text" size="small" @click="handleEdit(row)"></el-button>-->
						<el-button type="text" size="small" @click="handleDel(row)"></el-button>
					</span>
				</el-table-column>
			</el-table>
		</template>

		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom:10px;">
			<el-pagination @size-change="handleSizeChange"
 @current-change="handleCurrentChange" :current-page="1" :page-sizes="[10, 20, 30, 40]" :page-size="10" layout="total, prev, pager, next, jumper"
				:total="total" style="float:right">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog :title="editFormTtile" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<!--<el-select v-model="editForm.sex" placeholder="">
						<el-option label="" :value="1"></el-option>
						<el-option label="" :value="0"></el-option>
					</el-select>-->
					<el-radio-group v-model="editForm.sex">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="">
					<el-input-number v-model="editForm.phone" :min="0" :max="200"></el-input-number>
				</el-form-item>
				<el-form-item label="">
					<el-date-picker type="date" placeholder="" v-model="editForm.birthday"></el-date-picker>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" v-model="editForm.email"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"> </el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/util'
	import NProgress from 'nprogress'

  export default {
    data() {
      return {
	  			memberListUrl: function (obj){
	  				if(!obj.page)
	  					obj.page = 1;
	  				if(!obj.keywords)
	  					obj.keywords = '';
	  				
					return 'http://shama.jcjever.com/members?page=' + obj.page + '&keywords=' + obj.keywords;
				},
				memberRemoveUrl: function (obj){
					return 'http://shama.jcjever.com/members/' + obj.id;
				},
				formInline: {
					keywords: ''
				},
				pickerOptions0: {
					disabledDate(time) {
						return time.getTime() < Date.now() - 8.64e7;
					}
				},
				value1:'',
				editFormVisible:false,//
				editFormTtile:'',//
				//
				editForm: {
					id:0,
					name: '',
					sex: -1,
					phone: 0,
					birthday: '',
					email: ''
				},
				editLoading:false,
				btnEditText:' ',
				editFormRules:{
					name:[
						{ required: true, messphone: '', trigger: 'blur' }
					]
				},
				tableData: [],
				total:0,
				listLoading:false
     		}
    },
	mounted: function (){
		this.loadData({pphone:1});
	},
    methods: {
    		_export(){
    			alert('daochu');
    		},
    		search(){
    			if(this.formInline.keywords){
    				this.loadData({keywords:this.formInline.keywords});
    			}
    		},
			handleCurrentChange(val) {
				this.currentPage = val;
				console.log(`: ${val}`);
				this.loadData({pphone:val});
			},
			handleSizeChange(val) {
			   console.log(` ${val} `);
			},
			requestError:function(response) {
				this.$notify.error({
				  title: '',
				  messphone: ''
				});
			},
			removeData(obj){
				this.$http.delete(this.memberRemoveUrl(obj)).then((response) => {
					console.log('remove success');
				}).catch(this.requestError);
			},
			loadData(obj){
				this.$http.get(this.memberListUrl(obj)).then((response) => {
				 	this.tableData = response.body.data;
					this.total = response.body.total;
				}).catch(this.requestError);
			},
			//
			formatSex:function(row,column){
				return row.sex==0?'':row.sex==1?'':'';
			},
			formatBirth:function(row,column){
				return row.birthday= row.birthday.substring(0,10);;
			},
			//
			handleDel:function(row){
				//console.log(row);
				var _this=this;
				this.$confirm('?', '', {
					//type: 'warning'

				}).then(() => {
					_this.listLoading=true;
					NProgress.start();
					setTimeout(function(){
						_this.removeData({id:row.id});
						for(var i=0;i<_this.tableData.length;i++){
							if(_this.tableData[i].id==row.id){
								_this.tableData.splice(i,1);

								_this.listLoading=false;
								NProgress.done();
								_this.$notify({
									title: '',
									messphone: '',
									type: 'success'
								});

								break;
							}
						}
					},1000);
				}).catch(() => {

				});
			},
			//
			handleEdit:function(row){
				this.editFormVisible=true;
				this.editFormTtile='';
				this.editForm.id=row.id;
				this.editForm.name=row.name;
				this.editForm.sex=row.sex;
				this.editForm.phone=row.phone;
				this.editForm.birthday=row.birthday;
				this.editForm.email=row.email;
			},
			// or 
			editSubmit:function(){
				var _this=this;

				_this.$refs.editForm.validate((valid)=>{
					if(valid){

						_this.$confirm('','',{}).then(()=>{
							_this.editLoading=true;
							NProgress.start();
							_this.btnEditText='';
							setTimeout(function(){
								_this.editLoading=false;
								NProgress.done();
								_this.btnEditText=' ';
								_this.$notify({
									title: '',
									messphone: '',
									type: 'success'
								});
								_this.editFormVisible = false;

								if(_this.editForm.id==0){
									//
									_this.tableData.push({
										id:new Date().getTime(),
										name: _this.editForm.name,
										sex: _this.editForm.sex,
										phone: _this.editForm.phone,
										birthday: _this.editForm.birthday==''?'':util.formatDate.format(new Date(_this.editForm.birthday),'yyyy-MM-dd'),
										email: _this.editForm.email
									});
								}else{
									//
									for(var i=0;i<_this.tableData.length;i++){
										if(_this.tableData[i].id==_this.editForm.id){
											_this.tableData[i].name=_this.editForm.name;
											_this.tableData[i].sex=_this.editForm.sex;
											_this.tableData[i].phone=_this.editForm.phone;
											_this.tableData[i].birthday=_this.editForm.birthday==''?'':util.formatDate.format(new Date(_this.editForm.birthday),'yyyy-MM-dd');
											_this.tableData[i].email=_this.editForm.email;
											break;
										}
									}
								}
							},1000);

						});

					}
				});

			},
			//
			handleAdd:function(){
				var _this=this;

				this.editFormVisible=true;
				this.editFormTtile='';

				this.editForm.id=0;
				this.editForm.name='';
				this.editForm.sex=1;
				this.editForm.phone=0;
				this.editForm.birthday='';
				this.editForm.email='';
			}
    }
  }
</script>

<style scoped>
	.toolbar .el-form-item {
		margin-bottom: 10px;
	}

	.toolbar {
		background: #fff;
		padding: 10px 10px 0px 10px;
	}
</style>



