<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar">
			<el-form :inline="true" :model="formInline" class="demo-form-inline">
				<el-form-item>
					<el-input v-model="formInline.keywords" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button v-on:click="search"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<template>
			<el-table :data="tableData" highlight-current-row v-loading="listLoading" style="width: 100%;">
				<el-table-column type="index" width="50">
				</el-table-column>
				<el-table-column prop="email" label="" sortable>
				</el-table-column>
				<el-table-column prop="member_id"  width="100"  label="id" sortable>
				</el-table-column>
				<el-table-column prop="_status" label="" width="100"  :formatter="formaterStatus" sortable>
				</el-table-column>
				<el-table-column prop="_content" label="" :formatter="formatText" sortable>
				</el-table-column>
				<el-table-column prop="created_at" label="" :formatter="formatDate" width="160" sortable>
				</el-table-column>
				<el-table-column inline-template :context="_self" label="" width="150">
					<span>
						<el-button type="text" size="small" @click="handleDetail(row)"></el-button>
						<el-button :id="tableData.id" type="text" size="small" @click="handleDel(row)"></el-button>
					</span>
				</el-table-column>
			</el-table>
		</template>

		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom:10px;">
			<el-pagination @size-change="handleSizeChange"
 @current-change="handleCurrentChange" :current-page="1" :page-sizes="[10, 20, 30, 40]" :page-size="10" layout="total, prev, pager, next, jumper"
				:total="total" style="float:right">
			</el-pagination>
		</el-col>

		<el-dialog :title="detailFormTtile" v-model="detailFormVisible" :close-on-click-modal="false">
			<table >
				<tr><td></td><td>{{detailData.email}}</td></tr>
				<tr><td></td><td>{{detailData.content}}</td></tr>
				<tr><td></td><td>{{detailData.created_at}}</td></tr>
			</table>
		</el-dialog>

		<!---->
		<el-dialog :title="editFormTtile" v-model="editFormVisible" :close-on-click-modal="false" size="large" >
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">

				<el-form-item label="" prop="status">
					<el-radio-group v-model="editForm.status">
					    <el-radio :label="0"></el-radio>
					    <el-radio :label="1"></el-radio>
					    <el-radio :label="2"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="" prop="name">
					<el-input placeholder="" v-model="editForm.name"></el-input>
				</el-form-item>
				<el-form-item label="" prop="message">
					<el-input type="textarea" v-model="editForm.message"></el-input>
				</el-form-item>
				<el-form-item label="" prop="content">
					<ckeditor v-model="editForm.content" :id="editorA" :height="'300px'" :toolbar="[['Format']]"></ckeditor>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"> </el-button>
				<el-button type="primary" @click="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
			</div>
		</el-dialog>
	</section>
</template>


<script>
	import util from '../../common/util'
	import NProgress from 'nprogress'
	import Ckeditor from '../ckeditor.vue'
  	export default {
	  	components: {
			Ckeditor
		},
	    data() {
			return {
				ordersDetailUrl:function (obj){
					return 'http://shama.jcjever.com/feedbacks?page=1&active_id='+obj.id;
				},
				DetailUril: (obj) => {
					if(obj.id){
						return 'http://shama.jcjever.com/feedbacks/'+obj.id;
					}else{
						return 'http://shama.jcjever.com/feedbacks';
					}
				},
		  		ListUrl: function (obj){
	  				if(!obj.page)
	  					obj.page = 1;
	  				if(!obj.keywords)
	  					obj.keywords = '';
	  				return 'http://shama.jcjever.com/feedbacks?page=' + obj.page + '&keywords=' + obj.keywords;
				},
				formInline: {
					keywords: ''
				},
				pickerOptions0: {
					disabledDate(time) {
						return time.getTime() < Date.now() - 8.64e7;
					}
				},
				value1:'',
				editFormVisible:false,//
				detailFormVisible:false,//
				editFormTtile:'',//
				detailFormTtile:'',
				editorA: 'editor-a',
				//
				editForm: {
					id:0,
					status: 0,
					message: ''
				},
				editLoading:false,
				btnEditText:' ',
				editFormRules:{
					message:[
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				tableData: [],
				detailData:{
					content:'111'
				},
				total:0,
				listLoading:false
	     	};
	    },
		mounted: function (){
			this.loadData({page:1});
		},
	    methods: {
   			search(){
    			this.loadData({keywords:this.formInline.keywords});
    		},
			handleCurrentChange(val) {
				this.currentPage = val;
				console.log(`: ${val}`);
				this.loadData({page:this.currentPage,keywords:this.formInline.keywords});
			},
			handleSizeChange(val) {
			   console.log(` ${val} `);
			},
			requestError:function(response) {
	            this.$notify.error({
	              title: '',
	              message: response.body.message
	            });
	        },
			update(obj){
				this.$http.put(this.DetailUril(obj),obj).then((response) => {
					console.log('update success');
				}).catch(this.requestError);
			},
			removeData(obj){
				this.$http.delete(this.DetailUril(obj)).then((response) => {
					console.log('remove success');
				}).catch(this.requestError);
			},
			insert(obj,callback){
				this.$http.post(this.DetailUril(obj),obj).then((response) => {
					console.log('insert success');
					callback(response);
				}).catch(this.requestError);
			},
			loadData(obj){
				this.$http.get(this.ListUrl(obj)).then((response) => {
				 	this.tableData = response.body.data;
					this.total = response.body.total;
				}).catch(this.requestError);
			},
				//
			formatDate:function(row,column){
				if(row.created_at){
					return row.created_at = row.created_at.substring(0,10);
				}
			},
			formatText:function(row,column){
				row._content = row.content;
				if(row.content&&row.content.length>10){
					row._content = row.content.substring(0,15)+"...";
				}
				return row._content;
			},
			formaterStatus:function (row,column){
				switch(row.status){
					case 0:
						row._status='';
						break;
					case 1:
						row._status='';
						break;
					case 2:
						row._status='';
						break;
				}
				return row._status;
			},
   			handleDetail:function(row){
				this.detailFormVisible=true;
				//this.detailData = this.loadDetail(row.id);
				this.detailData.email = row.email;
				this.detailData.content = row.content;
				this.detailData.created_at = row.created_at;
			},
			loadDetail:function(id){
				this.$http.get(this.ordersDetailUrl({id:id})).then((response) => {
				 	this.detailData = response.body.data;
				}).catch(this.requestError);
			},
			//
   			handleDel:function(row){
				//console.log(row);
				var _this=this;
				this.$confirm('?', '', {
					//type: 'warning'
				}).then(() => {
					_this.listLoading=true;
					NProgress.start();
					setTimeout(function(){
						_this.removeData({id:row.id});
						for(var i=0;i<_this.tableData.length;i++){
							if(_this.tableData[i].id==row.id){
								_this.tableData.splice(i,1);
								_this.listLoading=false;
								NProgress.done();
								_this.$notify({
									title: '',
									messphone: '',
									type: 'success'
								});

								break;
							}
						}
					},1000);
				}).catch(() => {

				});
			},
			//
   			handleEdit:function(row){
				console.log(row.status);
				this.editFormVisible=true;
				this.editFormTtile='';
				this.editForm.id=row.id;
				this.editForm.content=row.content;
			},
			// or 
   			editSubmit:function(){
				var _this=this;
				//console.log(_this.$refs.editForm);
				_this.$refs.editForm.validate((valid)=>{
					console.log(valid);
					if(valid){
						_this.$confirm('','',{}).then(()=>{
							_this.editLoading=true;
							NProgress.start();
							_this.btnEditText='';
							setTimeout(function(){
								console.log(_this.editForm.id);
								if(0 == _this.editForm.id){
									_this.insert({id:_this.editForm.id,name:_this.editForm.name,status:_this.editForm.status,message:_this.editForm.message,content:_this.editForm.content},function(response){
										_this.tableData.push({
											id:new Date().getTime(),
											name:_this.editForm.name,
											status:_this.editForm.status,
											message:_this.editForm.message,
											created_at:response.body.active.created_at
										})
									});
								}else{
									_this.update({id:_this.editForm.id,name:_this.editForm.name,status:_this.editForm.status,message:_this.editForm.message,content:_this.editForm.content});
								}

								_this.editLoading=false;
								NProgress.done();
								_this.btnEditText=' ';
								_this.$notify({
									title: '',
									messphone: '',
									type: 'success'
								});
								_this.editFormVisible = false;
								if(_this.editForm.id==0){
									//

								}else{
									//
									for(var i=0;i<_this.tableData.length;i++){
										if(_this.tableData[i].id==_this.editForm.id){
											_this.tableData[i].name=_this.editForm.name;
											_this.tableData[i].status=_this.editForm.status;
											_this.tableData[i].message=_this.editForm.message;
											console.log(_this.editForm.status);
											console.log(_this.editForm.message);
											break;
										}
									}
								}
							},1000);
						});
					}
				});
			},
			//
   			handleAdd:function(){
				var _this=this;
				this.editFormVisible=true;
				this.editFormTtile='';
				this.editForm.id=0;
			}
		}
	}
</script>

<style scoped>
	.toolbar .el-form-item {
		margin-bottom: 10px;
	}

	.toolbar {
		background: #fff;
		padding: 10px 10px 0px 10px;
	}
</style>



